import { vaultProjectData } from "data/vaultProject";
import moment from "moment";
import { checkUnixYear } from "./dateDiffer";

export const projectDataCountParser = (type: string) => {
  let parsedDataCount = 0;

  for (let i = 0; i < vaultProjectData.length; i++) {
    if (type === "ongoing") {
      if (
        vaultProjectData[i].status === "ongoing" ||
        vaultProjectData[i].type.split("/")[1] === "use"
      )
        parsedDataCount += 1;
    } else {
      if (vaultProjectData[i].type.split("/")[1] === "nouse")
        parsedDataCount += 1;
    }
  }

  return parsedDataCount;
};

export const projectTotalPeriodCheck = () => {
  let totalTimestamp = 0;

  for (let i = 0; i < vaultProjectData.length; i++) {
    const endDate = moment(vaultProjectData[i].endDate).unix();
    const startDate = moment(vaultProjectData[i].startDate).unix();

    totalTimestamp += endDate - startDate;
  }

  return checkUnixYear(String(totalTimestamp));
};
