import moment from "moment";

export const checkDiffYear = (date: string) => {
  const today = moment();
  const compare = moment(date);
  const years = today.diff(compare, "years");

  compare.add(years, "years");

  const months = today.diff(compare, "months");

  return `${years}년 ${months}개월`;
};

export const checkUnixYear = (unix: string) => {
  const totalSeconds = Number(unix);
  const years = Math.floor(totalSeconds / 31536000);
  const remainingSeconds = totalSeconds % 31536000;
  const months = Math.floor(remainingSeconds / 2592000);

  return `${years}년 ${months}개월`;
};
